.dashboard{
	background-color: orange;
	width: fit-content;
	padding: 10px;
	margin-top: 5px;
	float: left;
	display: list-item;
	z-index: 100;
	position: fixed;
}
.dashboard-list{
	display: none;
}
.list{
	font-size: 18px;
	line-height: 30px;
	margin-bottom: 5px;
	margin-left: 0px;
	padding-left: 0px;
}
.list-item{
	padding-left: 50px !important;
	padding-right: 20px !important;
	margin:5px;
	background-clip: padding-box;
	padding: 5px;
}
.list-item:hover{
	background-color: #ff6600;
	font-size:20px; 
	cursor: pointer;
	background-clip: padding-box;
}
.dashboard-title{
	top: 0px;
	margin-top: 5px;
	font-size: 25px;
	background-color:  #ff8533;
	width: 100%;
	background-clip: padding-box;
	padding-left: 50px !important;
	padding: 5px;
}
.dashboard-button{
	text-decoration: none;
	background-color: orange;
	border:none;
}
.dashboard-button:hover{
  background-color: #ff6600;
}
ul,li{
	text-decoration: none;
	list-style: none;
}
a{
	text-decoration: none !important;
}
.block{
	display: block !important;
}.
.sublist{
	float: left !important;
	padding: 5px;
	padding-left: 0px !important;
	margin-left: 0px !important;
}
@media screen and (max-width: 500px){
	.dashboard{
	padding: 5px;
	margin-top: 5px;
	float: left;
	display: list-item;
	z-index: 100;
	position: fixed;
}
.dashboard-list{
	display: none;
}
.list{
	font-size: 15px;
	line-height: 20px;
	margin-bottom: 5px;
	margin-left: 0px;
	padding-left: 0px;
}
.list-item{
	padding-left: 30px !important;
	padding-right: 10px !important;
	margin:5px;
	background-clip: padding-box;
	padding: 5px;
}
.list-item:hover{
	background-color: #ff6600;
	font-size:18px; 
	cursor: pointer;
	background-clip: padding-box;
}
.dashboard-title{
	top: 0px;
	margin-top: 5px;
	font-size: 20px;
	background-color:  #ff8533;
	width: 100%;
	background-clip: padding-box;
	padding-left: 30px !important;
	padding: 5px;
}
}